.compare {
  &__title {
    margin-bottom: 48px;

    @include on-desktop {
      margin-bottom: 56px;
    }
  }

  &__products {
    @include page-grid;

    row-gap: 40px;
  }

  &__product {
    grid-column: 1 / -1;

    @include on-tablet {
      grid-column: 2 / -2;
    }

    @include on-desktop {
      grid-column: span 4;
    }
  }

  &__descripe {
    font-weight: 400;
    font-size: 16px;
    line-height: 22.4px;
  }

  &__price {
    font-weight: 700;
    font-size: 24px;
    line-height: 33.6px;
  }
}
